<template>
    <div class="updatePassword">
        <div class="updateItem">
            <span>原始密码：</span>
            <el-input placeholder="请输入旧密码" v-model="oldPassword" show-password></el-input>
        </div>
        <div class="updateItem">
            <span>新密码：</span>
            <el-input placeholder="请输入新密码" v-model="newPassword" show-password></el-input>
        </div>
        <el-button type="primary" class="updateBtn" @click="updatePassword">修改</el-button>
    </div>
</template>

<script>
import { get_user_updatePwd_api } from '@/api/system';
export default {
    data() {
        return {
            oldPassword: '', // 旧密码 
            newPassword: '' // 新密码
        }
    },
    methods: {
        // 更新密码
        updatePassword() {
            if ( this.oldPassword == '' || this.newPassword == '') {
                this.$msssage({
                    message: "请输入密码",
                    type: 'warning'
                })
                return;
            } 
            let obj = {
                oldPassword: this.oldPassword,
                newPassword: this.newPassword
            }
            get_user_updatePwd_api(obj)
            .then(() => {
                this.$msssage({
                    message: "修改成功",
                    type: 'success'
                })
            })
        }
        
    }
}
</script>

<style scoped lang='scss'>
.updatePassword{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .updateItem{
        display: flex;
        margin-bottom: 20px;
        justify-content: center;
        align-items: center;
        span{
            display: block;
            width: 200px;
        }
    }
    .updateBtn{
        width:300px;
    }
}
</style>